import React from "react"
import Navbar from "../components/navbar"
import Blog from "../components/blog"
import BlogVideo from "../components/blog_video"
import Graph from "../components/graph"

import Container from 'react-bootstrap/Container'
import {Jumbotron, Row, Col} from "react-bootstrap"

export default function Home() {
  return (
    <div>
      <link
        rel="stylesheet"
        href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
        integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
        crossorigin="anonymous"
      />

      <Navbar items={['Blog', 'Resume', 'Projects']} links={['', 'resume', 'projects']} projects={['Ultra Stack', 'Shaders', 'Puzzle Game', 'IK Demos', 'Spline Editor']}/>    

      <Container fluid>
        <br/>
        <Jumbotron>
          <Row>

            <Col>
            <h1>Greetings Traveler</h1>
              <p>
                I'm Gabe - a software engineer and game designer. <br/>
                This is my corner of the internet where I share what I'm working on and organize
                things I've done in the past.
              </p>
            </Col>
          </Row>

        </Jumbotron>

     

      </Container>

      <Container fluid>
        <BlogVideo date="4/24/2021" title="NATURAL Engine Map Editor" body="I've been working on a tile-based 2D engine using C++, OpenGL, and SDL2 (for input/audio). For now, I'm calling it NATURAL Engine. This is a short demo
                                                                          of the engine's map editor. Not shown in this video is its ability to load and save maps in a binary format." video="https://www.youtube.com/embed/rV9T1dKSBs0" />
        <br/>

        <Blog date="02/09/2021" title="First post." body="Welcome! I guess this is my first post." />
        <br/>

      </Container>

      <br/>
    </div>
  )
}
